@import "reset";
@import "fonts";
@import "mixins";
@import "variables";

// ----------------------index----------------------
.container {
	width: 100%;
	padding: 0 15px;
	max-width: 1190px; }
.hidden-mobile {
	display: none; }
.btn {
	background-color: #1569b3;
	text-align: center;
	padding: 25px;
	border: none;
	color: #fff;
	text-transform: uppercase;
	font-family: $regularFont;
	font-weight: bold;
	font-size: 17px;
	cursor: pointer;
	&:hover {
		background-color: #f7b516;
		transition: background-color .3s linear; }
	&:focus {
		outline: none; } }
.btn--sm {
	font-size: 12px;
	padding: 15px 20px; }
.btn--second-st {
	background-color: #f7b516;
	&:hover {
		background-color: #1569b3;
		transition: background-color .3s linear; } }
.input {
	padding: 25px 33px;
	background-color: #f2f2f2; }
.text_uppercase {
	text-transform: uppercase; }
.text_bold {
	font-weight: bold; }
.form-icon {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 100%;
		width: 30px;
		flex-shrink: 0;
		font-size: 20px;
		color: #818181;
		margin-left: 15px;
		cursor: pointer; }
//------------------header---------------------
.header {
	background-color: #191919;
	padding: 20px 0;
	color: #fff;
	text-align: center;
	.logo {
		margin-bottom: 20px; }
	&__work {
		font-family: $regularFont;
		font-size: 15px;
		&__time {
			font-weight: bold;
			margin-bottom: 5px; } }
	.order {
		&-tel-wrap {
			font-family: $blackFont;
			font-size: 18px;
			margin-right: 10px; } } }
//------------------banner---------------------
.banner {
	position: relative;
	min-height: 400px;
	padding: 20px 0;
	background: url("../../build/img/banner-bg.jpg") no-repeat center right;
	background-size: cover;
	color: #000000;
	text-align: center;
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0px;
		left: 0px;
		bottom: 0px;
		right: 0px;
		background-color: rgba(247, 181, 22, .7);
		z-index: 1; }
	&__txt {
		position: relative;
		z-index: 2; }
	&__title {
		font-size: 30px;
		line-height: 1em;
		font-family: $blackFont;
		margin-bottom: 20px; }
	&__subtitle {
		font-size: 20px;
		font-family: $extbldFont;
		margin-bottom: 30px; }
	&-list {
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		max-width: 430px;
		margin: 0 auto;
		&__item {
			display: flex;
			text-align: left;
			align-items: center;
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0px; } }
		&__icon {
			flex-basis: 50px;
			flex-shrink: 0;
			margin-right: 15px; }
		&__text {
			font-size: 15px;
			font-family: $regularFont;
			flex-grow: 1; } } }
//-----------------price------------------------
.price {
	padding: 30px 0;
	&-wrap {
		text-align: center; }
	&__title {
		@include title-sm; }
	&__input {
		display: flex;
		align-items: center;
		margin-bottom: 20px;
		input {
			&::-webkit-input-placeholder {
				text-transform: uppercase;
				font-size: 13px;
				color: #808080; }
			&::-moz-placeholder {
				text-transform: uppercase;
				font-size: 13px;
				color: #808080; }
			&:-ms-input-placeholder {
				text-transform: uppercase;
				font-size: 13px;
				color: #808080; } } }
	&__txt {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		color: #000;
		background-color: #fff;
		font-size: 14px;
		margin-bottom: 20px;
		span {
			margin-bottom: 15px; }
		.form-icon {
			color: #000;
			margin-left: 0px; } }
	&__btn {
		width: 100%; } }
//-------------------property---------------------------
.property {
	padding: 50px 0;
	background-color: #08243e;
	color: #fff;
	text-align: center;
	&__title {
		@include title; }
	&__subtitle {
		font-size: 20px;
		margin-bottom: 30px; }
	&__item {
		margin-bottom: 30px;
		&:last-child {
			margin-bottom: 0px; } }
	&__img {
		min-height: 130px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px; } }
//------------------search---------------------
.search {
	background-color: #152f53;
	padding: 50px 0;
	text-align: center;
	color: #fff;
	&__header {
		margin-bottom: 20px; }
	&__title {
		@include title;
		margin-bottom: 20px; }
	&__subtitle {
		font-size: 16px; }
	&__form {
		display: flex;
		flex-direction: column;
		margin-bottom: 20px; }
	&__input {
		margin-bottom: 20px;
		display: flex;
		align-items: center; }
	&__descr {
		font-size: 16px;
		margin-bottom: 20px; }
	&__tel {
		&-descr {
			font-size: 16px; }
		&-number {
			margin-top: 10px;
			font-family: $blackFont;
			font-size: 20px; } } }
//------------------guarantee---------------------
.guarantee {
	background-color: #fff;
	padding: 50px 0;
	text-align: center;
	&__title {
		@include title-sm;
		margin-bottom: 20px; }
	&__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px; } }
	&__img {
		position: relative;
		height: 300px;
		width: 230px;
		overflow: hidden;
		margin: 0 auto 20px;
		img {
			height: 100%;
			max-width: none;
			min-width: 100%;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%); } }
	&__txt {
		font-size: 16px;
		margin-bottom: 15px; }
	&__link {
		text-decoration: underline;
		color: #000;
		font-weight: bold;
		font-size: 16px;
		&:hover {
			text-decoration: none; } } }
//------------------bonus---------------------
.bonus {
	padding: 50px 0px;
	background-color: #08243e;
	color: #fff;
	text-align: center;
	&__title {
		@include title-sm;
		margin-bottom: 30px; }
	&__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px; } }
	&__circle {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100px;
		height: 100px;
		margin: 0px auto 20px;
		border: 2px solid #fff;
		border-radius: 50%;
		font-size: 30px;
		span {
			font-size: 35px; } } }
//-------------------questions--------------------
.questions {
	padding: 50px 0;
	background-color: #0b3554;
	color: #fff;
	&__header {
		text-align: center;
		margin-bottom: 30px; }
	&__wrap {
		text-align: center; }
	&__text {
		text-align: left;
		margin: 0px auto 20px;
		max-width: 480px; }
	&__img {
		text-align: center;
		position: relative;
		display: inline-block; }
	&__person {
		text-align: left;
		padding: 5px 20px;
		background-color: rgba(11,53,84, .7);
		position: absolute;
		left: 0px;
		bottom: 0px; }
	&__person-name {
		color: #fff;
		font-size: 14px; }
	&__title {
		@include title-sm;
		margin-bottom: 25px; }
	&__subtitle {
		font-size: 20px; }
	&__list {
		padding-left: 25px;
		list-style-type: decimal;
		font-size: 14px;
		margin-bottom: 20px; }
	&__list-item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px; } }
	&__link {
		display: block;
		margin-left: 15px;
		color: #fff;
		font-size: 14px;
		text-decoration: underline;
		&:focus {
			text-decoration: none;
			color: #579eff;
			transition: color .3s linear; } }
	&__link--sm {
		font-size: 12px; }
	&__hover {
		display: none; }
	&__more {
		color: #fff;
		display: flex;
		align-items: center;
		padding-left: 8px;
		.fa {
			margin-right: 8px; } } }
//-----------------work---------------------
.work {
	padding: 50px 0px;
	text-align: center;
	&__header {
		margin-bottom: 30px; }
	&__title {
		@include title;
		margin-bottom: 20px; }
	&__subtitle {
		font-size: 20px; }
	&__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px; } }
	&__icon {
		height: 160px;
		display: flex;
		justify-content: center;
		align-items: center; } }
//-----------------storage---------------------
.storage {
	padding: 50px 0px;
	background-color: #0b3554;
	&__header {
		text-align: center;
		color: #fff;
		margin-bottom: 30px; }
	&__title {
		@include title-sm;
		margin-bottom: 20px; }
	&__subtitle {
		font-size: 20px; }
	&__img {
		padding: 0px 10px; }
	&__slider {
		.slick-dots {
			bottom: -30px;
			li {
				width: 14px;
				height: 14px;
				border: 2px solid #fff;
				border-radius: 50%;
				button {
					width: 100%;
					height: 100%;
					background-color: #fff;
					border-radius: 50%; } }
			.slick-active {
				button {
					background-color: #f7b516; } } }
		.slick-prev, .slick-next {
			background-color: #000;
			z-index: 1;
			cursor: pointer;
			opacity: .7;
			&:hover {
				opacity: 1; } }
		.slick-prev {
			background: url('../../build/img/slider-arrow-prev.png') no-repeat center;
			left: 30px; }
		.slick-next {
			background: url('../../build/img/slider-arrow-next.png') no-repeat center;
			right: 30px; } } }
//-----------------fix---------------------
.fix {
	padding: 50px 0px;
	text-align: center;
	&__title {
		@include title-sm;
		max-width: 720px;
		margin: 0 auto 30px; }
	&__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0px; } }
	&__icon {
		width: 170px;
		height: 170px;
		margin: 0 auto 20px;
		border: 5px solid #ffbc3b;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%; } }
//-----------------advice---------------------
.advice {
	padding: 50px 0px;
	background-color: #0d324d;
	color: #fff;
	text-align: center;
	&__title {
		@include title-sm;
		margin-bottom: 30px; }
	&__tel {
		margin-bottom: 30px;
		&-txt {
			font-size: 14px; }
		&-number {
			margin-top: 5px;
			font-size: 18px;
			font-family: $blackFont; } }
	&__input {
		display: flex;
		align-items: center;
		margin-bottom: 15px; }
	&__btn {
		width: 100%; } }
//-----------------office---------------------
.office {
	padding: 50px 0px;
	text-align: center;
	&__title {
		@include title-sm;
		margin-bottom: 30px; }
	&__adress {
		font-size: 18px;
		margin-bottom: 20px; }
	&__tel {
		text-transform: uppercase;
		font-size: 20px;
		font-family: $blackFont; } }
//-----------------map------------------------
.map {
	max-width: 1920px;
	margin: 0 auto;
	min-height: 350px;
	background: url('../../build/img/map-img.png') no-repeat center center;
	background-size: cover; }
//-----------------footer------------------------
.footer {
	padding: 30px 0px;
	background-color: #191919;
	color: #fff;
	text-align: center;
	&__logo {
		display: inline-block;
		margin-bottom: 20px; }
	&__copy {
		margin-bottom: 20px;
		font-size: 12px;
		text-transform: uppercase;
		&-txt {
			display: inline-block;
			vertical-align: middle; }
		.fa {
			margin-left: 5px;
			color: #ff463e;
			margin-right: 3px; }
		&-logo {
			margin-left: 20px;
			display: inline-block;
			vertical-align: middle; } }
	&__tel {
		&-txt {
			font-size: 13px; }
		&-number {
			margin-top: 10px;
			font-size: 18px;
			text-transform: uppercase;
			font-family: $blackFont; } } }
//-----------------media---------------------
@media screen and (min-width: 480px) {
