		&-wrap {
			display: flex;
			justify-content: space-between;
			align-items: center; }
		.logo {
			margin-bottom: 0px;
			margin-right: 5px; } }
	.property {
		&__wrap {
			display: flex;
			flex-wrap: wrap; }
		&__item {
			width: 50%;
			padding: 0 20px;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 30px; } } }
	.search {
		&__form {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: center;
			margin-left: -2%; }
		&__input {
			width: 48%;
			margin-left: 2%; }
		&__btn {
			width: 48%; } }
	.bonus {
		&-wrap {
			display: flex;
			flex-wrap: wrap; }
		&__item {
			width: 50%;
			padding: 0 10px;
			&:last-child {
				margin-bottom: 20px; } } }
	.work {
		&__wrap {
			display: flex;
			justify-content: center;
			flex-wrap: wrap; }
		&__item {
			width: 50%;
			padding: 0px 20px;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0px; } } }
	.fix {
		&__wrap {
			display: flex;
			justify-content: center;
			flex-wrap: wrap; }
		&__item {
			width: 50%;
			padding: 0 15px; } }
	.advice {
		&__form {
			max-width: 770px;
			margin-left: auto;
			margin-right: auto;
			display: flex;
			justify-content: space-between; }
		&__input {
			width: 49%;
			margin-right: 2%;
			flex: 1;
			margin-bottom: 0px; }
		&__btn {
			width: 49px;
			flex: 1; } } }
@media screen and (min-width: 600px) {
	.hidden-mobile {
		display: block; }
	.hidden-devices {
		display: none; }
	.order {
		display: flex;
		align-items: center; }
	.price {
		width: 100%;
