		&__btn {
			width: 48%;
			margin-left: 2%; } }
	.guarantee {
		&__wrap {
			display: flex;
			justify-content: center;
			flex-wrap: wrap; }
		&__item {
			width: 50%;
			padding: 0 15px;
			margin-bottom: 20px; } } }
