		padding: 90px 0px;
		&__title {
			margin-bottom: 60px; }
		&__tel {
			margin-bottom: 60px;
			&-number {
				font-size: 30px; } } }
	.office {
		padding: 90px 0px;
		&__title {
			margin-bottom: 100px; }
		&__adress {
			margin-bottom: 30px; }
		&__tel {
			font-size: 24px; } }
	.map {
		min-height: 380px; }
	.footer {
		&__wrap {
			display: flex;
			justify-content: space-between;
			align-items: center; }
		&__logo {
			margin-bottom: 0px; }
		&__copy {
			margin-bottom: 0px; } } }
@media screen and (min-width: 1000px) {
	.container {
		padding: 0 10px;
		max-width: 1190px;
		margin: 0 auto; }
	.banner {
		min-height: 860px;
		&__txt {
			padding: 100px 90px;
			width: 570px; }
		&__subtitle {
			margin-bottom: 90px; }
