		background: #152f53 url('../../build/img/search-bg.jpg') no-repeat center;
		background-size: cover; }
	.guarantee {
		&__item {
			padding: 0 30px; } }
	.bonus {
		padding: 90px 0px;
		&__title {
			margin-bottom: 80px; }
